import axios from 'axios';
import env from '../config/env';
var qs = require('qs');

let util = {};
const ajaxUrl = "/upms";

util.ajax = axios.create({
    baseURL: ajaxUrl,
    timeout: 30000
});
util.ajax.interceptors.request.use(function (response) {
	console.log(response);
	return response;
});
util.params = function (obj){
	return qs.stringify(obj);
};


export default util;



