<template>
    <Breadcrumb>
        <BreadcrumbItem 
            v-for="item in currentPath"
            :href="item.path" 
            :key="item.name"
        >{{item.title}}</BreadcrumbItem>
    </Breadcrumb>
</template>

<script>
export default {
    name: 'breadcrumbNav',
    props: {
        currentPath: Array
    },
    methods: {

    }
};
</script>




